const canvas = document.getElementById("canvas1");
const ctx = canvas.getContext("2d");
const canvas_width = (canvas.width = 1280);
const canvas_height = (canvas.height = 720);

const sprite = new Image();
sprite.src = "src/zombie.png";

function scaleoffsetX(offset: number, scale: number) {
  return offset + scale * canvas_width;
}
function scaleoffsetY(offset: number, scale: number) {
  return offset + scale * canvas_height;
}

ctx.drawImage(sprite, 0, 0, scaleoffsetX(0, 0.5), scaleoffsetY(0, 0.5));
